import { Component, OnInit } from '@angular/core';
import { AbstractComponent } from '../abstract-component';
import { ColonyAction } from './colony-action';
import { ColonyActionsService } from './colony-actions.service';
import { DiaSemana } from './dia-semana';

@Component({
	selector: 'app-colony-actions',
	templateUrl: './colony-actions.component.html',
	styleUrls: ['./colony-actions.component.less']
})
export class ColonyActionsComponent extends AbstractComponent implements OnInit {

	readonly TIME_NOW = new Date(new Date().getFullYear(),new Date().getMonth(),new Date().getDate(),0);
	readonly TIMESTAMP_PLUS = 60 * 60 * 1000;
	readonly TIME_05 = 5 * 60 * 1000;
	readonly TIME_59 = 59 * 60 * 1000 + 59 * 1000;

	constructor( private colonyActionsService: ColonyActionsService) { 
		super();
	}

	get dias () {
		return this.colonyActionsService.dias;
	}

	readonly LOCAL_TIMEZONE_STRING = new Date().toTimeString().substr (new Date().toTimeString().search('GMT'));
	readonly LOCAL_TIMEZONE_OFFSET = - new Date().getTimezoneOffset() * 60 * 1000;

	diaSelecionadoId:any = "";
	timezoneOffset:any = "";
	eventoSelecionadoId:any = "";

	diaSelecionado:DiaSemana = DiaSemana.generate();
	eventoSelecionado:ColonyAction = ColonyAction.generate();
	timezoneSelecionado:number = 0;

	selecionaDia() {
		this.diaSelecionado = this.colonyActionsService.diasIndex.get(this.diaSelecionadoId);
	}

	selecionarEvento(evento:ColonyAction) {
		this.eventoSelecionado = evento.clone();
	}

	selecionaTimezone() {
		this.timezoneSelecionado = this.timezoneOffset ? parseInt(this.timezoneOffset) : 0;
	}

	getDateLine(sequencial) {
		var start = new Date(this.TIME_NOW.getTime() + this.timezoneSelecionado + (sequencial*this.TIMESTAMP_PLUS) + this.TIME_05 ).toTimeString().slice(0,5) ;
		var end = new Date(this.TIME_NOW.getTime() + this.timezoneSelecionado + (sequencial*this.TIMESTAMP_PLUS) + this.TIME_59 ).toTimeString().slice(0,5);
		return start+ " ~ " + end;
	}

	isInnerEvent(sequencial) {
		if (this.diaSelecionadoId != new Date(new Date().getTime() - this.LOCAL_TIMEZONE_OFFSET).getDay())
			return false;
		var start = new Date(this.TIME_NOW.getTime() + this.LOCAL_TIMEZONE_OFFSET + (sequencial*this.TIMESTAMP_PLUS) );
		var end = new Date(this.TIME_NOW.getTime() + this.LOCAL_TIMEZONE_OFFSET + (sequencial*this.TIMESTAMP_PLUS) + this.TIME_59 );
		return new Date().getTime()  >= start.getTime() && new Date().getTime()  <= end.getTime();
	}

	ngOnInit() {
		this.timezoneOffset = this.LOCAL_TIMEZONE_OFFSET;
		this.selecionaTimezone();

		this.editavel = true;
		
		this.diaSelecionadoId = new Date(new Date().getTime() - this.LOCAL_TIMEZONE_OFFSET).getDay();
		
		this.selecionaDia();
	}

}
