import { ArrayIndexador } from "../array-indexador";
import { ColonyAction } from "./colony-action";

export class DiaSemana {
    id: number;
    day:string;
    event:string;

    colonyActionsIndex: ArrayIndexador<ColonyAction> = null;
    
	constructor(id:(number|any), day?:string, event?: string) {
        if (typeof id == "number") {
            this.id = id;
            this.day = day;
            this.event = event;
        }
        else {
            this.id = parseInt(id['id']);
            this.day = id['day'];
            this.event = id['event'];
        }
    }

    setColonyActions(colonyActions) {
        this.colonyActionsIndex = new ArrayIndexador(colonyActions, 'sequencial');
    }
    
    static generate() {
        return new DiaSemana(0, "", "");
    }
    
    clone() {
        return new DiaSemana(this.id, this.day, this.event);
    }
}