	diasIndex:ArrayIndexador<DiaSemana> = null;

	constructor() {
		this.obtemDias();
	}

	obtemDias() {
		this.diasIndex = new ArrayIndexador(this.dias);
		for (var i = 0; i < this.colonyActions.length; i++) {
			var ca = this.colonyActions[i];
			var di = this.diasIndex.get(ca.dia_semana_id);
			if (di) {
				if (di.colonyActionsIndex) {
					di.colonyActionsIndex.add(ca);
				}
				else {
					di.colonyActionsIndex = new ArrayIndexador([ca], 'sequencial');
				}
			}
		}
	}
